import { Component, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { StoreAbstract } from '../../core/classes/store.abstract';
import { SwiperComponent } from '../../shared/components/atoms/swiper/swiper.component';
import { TextComponent } from '../../shared/components/atoms/text/text.component';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrl: './main.component.scss'
})
export class MainComponent extends StoreAbstract implements OnInit {

  @ViewChild('container', { read: ViewContainerRef, static: true }) container!: ViewContainerRef;

  private ownData!: any;
  private index!: number;

  ngOnInit() {
    super.OnResizeRedux();
    this.index = 0;
    const value = this.logicService.getLogicValues('main');
    this.value(this.index, value);
  }

  public speak(data: any) {
    this.factoryService.createComponent(
      TextComponent,
      this.container,
      data.node.result[0]
    );
    return Promise.resolve(++this.index).then((index) => {
      this.value(index);
    });
}

  public slot(data: any) {
    this.factoryService.createComponent(
      SwiperComponent,
      this.container,
      data.node.result
    );
    return Promise.resolve(++this.index).then((index) => {
      this.value(index);
    });
  }

  public async value(index: number, data?: any) {
    if(data == null) return;
    if (index === 0) this.ownData = data;
    if (index === this.ownData.length) return;
    const node = this.ownData[index].node.category;
    await (this as any)[node](this.ownData[index]);
  }

}
